import React, { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'

import {
  Hidden,
  AppBar,
  Toolbar,
  Tabs,
  Tab,
  Button,
  Grid,
  Collapse,
  Divider
} from '@material-ui/core'

import logo from '../assets/images/final-logo-file-01.svg'

import MenuRoundedIcon from '@material-ui/icons/MenuRounded';

import { makeStyles } from '@material-ui/core/styles'
import { headerStyles } from './ui/Styles'
const useStyles = makeStyles(headerStyles)

export default function Header() {
  const classes = useStyles()
  const [tabValue, setTabValue] = useState(2)

  const [open, setOpen] = React.useState(false);

  useEffect(() => {
    switch(window.location.pathname) {
      default: break
      case '/':
        if(tabValue !== 2)
          setTabValue(2)
        break
      case '/about':
        if(tabValue !== 0)
          setTabValue(0)
        break
      case '/contact':
        if(tabValue !== 1)
          setTabValue(1)
        break
    }
  })

  const handleTabChange = (e, newValue) => setTabValue(newValue)

  const tabs = [
    {
      label: 'About Us',
      route: '/about',
    },
    {
      label: 'Contact Us',
      route: '/contact',
    },
    {
      label: null,
      route: '/',
    },
  ]
  
  return (
    <Grid item xs={12} sm={12} md ={12} lg={12} xl={12}>
    <AppBar 
      color='secondary'
      position='relative'
      className={classes.appBar}
    >
      <Toolbar className={classes.toolbar, 'headerbar'} style={{width:'auto', paddingTop:'5px'}}>
        <Button
          disableRipple
          class="navbarri"
          classes={"navbarri"}
          className={classes.logoButton, 'navbarri'}
          onClick={() => setTabValue(2)}
          component={Link}
          to='/'
          onClick={()=>{if(open) setOpen(!open);}}
        >
          <img 
            alt='logo'
            src={logo}
            className={classes.logoImg}
          />
        </Button>
        <Tabs
          value={tabValue}
          onChange={handleTabChange}
          aria-label='main nav'
          className={classes.nav, 'headerlink'}
          classes={{
            flexContainer: classes.navFlexContainer,
            indicator: classes.tabIndicator,
          }}
          
        >
          {
            tabs.map((tab, i) => (
              <Hidden only={['xs','sm']}>
                <Tab
                key={i}
                disableRipple
                label={tab.label}
                className={classes.tab}
                classes={{
                  selected: classes.selectedTab,
                }}
                component={Link}
                to={tab.route}
                style={{
                  display: i === 2 ? 'none' : undefined
                }}
              />
              </Hidden>
              
              
            ))
          }
        </Tabs>
        <Hidden only={['md','lg', 'xl']}>
        <Button
          disableRipple
          className={classes.logoButton}
          onClick={()=>{setOpen(!open);}}
        >
          <MenuRoundedIcon />
        </Button>
        </Hidden>
      </Toolbar>
      <Hidden only={['md', 'lg', 'xl']}>
      <Collapse in={open} timeout="auto" unmountOnExit>
          <ul style={{listStyle: 'none'}}>
                <li><Tab
                disableRipple
                label={'About Us'}
                className={classes.tab}
                component={Link}
                to={'/about'}
                onClick={()=>{setOpen(!open);}}
              /><Divider orientation="horizontal"></Divider>
              </li><li><Tab
                disableRipple
                label={'Contact Us'}
                className={classes.tab}
                component={Link}
                to={'/contact'}
                onClick={()=>{setOpen(!open);}}
              /></li></ul></Collapse>
      </Hidden>
    </AppBar>
    </Grid>
  )
}
